@media screen and (max-width: 992px) {
  .brk-card-out:hover .brk-card-out__link {
    margin-left: 50%;
    margin-top: 20px;
    transform: translate(-50%, 0);
    width: 70px;
    height: 70px;
  }
  .brk-card-out:hover .brk-card-out__info {
    left: 20px;
    top: calc(40px + 70px);
    transform: translate(0);
  }
}

@media screen and (max-width: 768px) {
  .brk-card-out .brk-card-out__overlay {
    background-color: rgba(0, 0, 0, 0.4);
  }
  .brk-card-out .brk-card-out__link {
    margin-left: 50%;
    margin-top: 20px;
    transform: translate(-50%, 0);
    width: 70px;
    height: 70px;
    opacity: 1;
  }
  .brk-card-out .brk-card-out__info {
    left: 20px;
    top: calc(40px + 70px);
    transform: translate(0);
    opacity: 1;
  }
}

.brk-square-dots .slick-dots li {
  border-radius: 0;
  background: transparent;
  border: 2px solid var(--brand-primary) !important;
  width: 8px;
  height: 8px;
}

.brk-square-dots .slick-dots li.slick-active {
  width: 13px;
  height: 13px;
  border: 2px solid var(--brand-primary);
  background-color: var(--brand-primary);
}
